button {
    background-color: #ffffff;
    border: none;
    font-family: 'Segoe UI',sans-serif;
    font-weight: 500;
    font-size: 1.25rem;
    border-radius: 2rem;
    padding: 0.5rem 1.7rem;
    color: #4285F4;
    cursor: pointer;
}

button:hover {
    transition: .5s ease-in-out;
}