.contact-form {
    min-height: 100vh;
}

.contain {
  background-color: blueviolet;
  max-width: 1170px;
  margin-left: auto;
  margin-right: auto;
  padding: 1em;
}

div.form {
  background-color: #eee;
}
.contact-wrapper {
  margin: auto 0;
}

.submit-btn {
  float: left;
}
.reset-btn {
  float: right;
}

.form-headline:after {
  content: "";
  display: block;
  width: 10%;
  padding-top: 10px;
  border-bottom: 3px solid #ec1c24;
}

.highlight-text {
  color: #ec1c24;
}

.hightlight-contact-info {
  font-weight: 700;
  font-size: 22px;
  line-height: 1.5;
}

.highlight-text-grey {
  font-weight: 500;
}

.email-info {
  margin-top: 20px;
  font-weight: 500;
}

::-webkit-input-placeholder {
  /* Chrome */
  font-family: "Segoe UI", sans-serif;
}

.required-input {
  color: black;
}
@media (min-width: 600px) {
  .contain {
    padding: 0;
  }
}

h3,
ul {
  margin: 0;
}

h3 {
  margin-bottom: 1rem;
}

input,
textarea {
  font-family: "Segoe UI", sans-serif;
  font-size: 1rem;
}

.form-input:focus,
textarea:focus {
  outline: 2px solid blueviolet;
}

.form-input,
textarea {
  width: 100%;
  border: 1px solid #bdbdbd;
  border-radius: 5px;
}

.wrapper > * {
  padding: 1em;
}
@media (min-width: 700px) {
  .wrapper {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
  .wrapper > * {
    padding: 2em 2em;
  }
}

ul {
  list-style: none;
  padding: 0;
}

.contacts {
  color: #212d31;
}

.form {
  background: #fff;
}

form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
}
form label {
  display: block;
}
form p {
  margin: 0;
}

.full-width {
  grid-column: 1 / 3;
}

.btn,
.submit-btn,
.form-input,
textarea {
  padding: 1em;
}

.submit-btn,
.reset-btn {
  color: white;
  font-size: 18px;
  width: 25%;
  margin: 8px 0;
  border: none;
  font-weight: 500;
  border-radius: 50px;
  text-transform: uppercase;
  background-color: blueviolet;
  padding: 14px 20px;
  cursor: pointer;
  /*border-radius: 5px;*/
}
.button1:hover {
  /*transition: .5s;*/
  background-color: #253da1;
}

.btn:hover,
.submit-btn:hover,
.btn:focus,
.submit-btn:focus {
  opacity: 0.8;
  color: black;
  outline: 0;
  color: #eee;
}
.error {
  color: #ec1c24;
}

.font1 {
    font-size: 1.2rem;
}
